import React from "react";

class Checkbox extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      checkboxTranslate: "translateX(30px)",
      value: true
    };
  }
  
  clicked = () => {
    this.state.value === true
      ? this.setState({
          checkboxTranslate: "translateX(30px)",
          value: false
        })
      : this.setState({
          checkboxTranslate: "translateX(0px)",
          value: true
        });
    this.props.action(this.state.value);
  };

  render() {
    return (
      <div className="checkbox-wrapper" onClick={this.clicked}>
        <div
          className="checkbox"
          style={{ transform: `${this.state.checkboxTranslate}` }}
        />
      </div>
    );
  }
}

export default Checkbox;
