import React from "react";
import Checkbox from "./Checkbox";
import Slider from "./Slider";

class Display extends React.Component {

  render() {
    return (
      <div id="displayButtons">
        <div id="power">
          <p style={{ gridArea: "1/1/2/5", marginBottom: 0 }}>Power</p>
          <p>OFF</p>
          <Checkbox action={this.props.turnPower} />
          <p>ON</p>
        </div>
        <div id="volume">
          <p style={{ gridArea: "1/1/2/5", marginBottom: 0 }}>Volume</p>
          <p>-</p>
          <Slider action={this.props.volumeAdj} />
          <p>+</p>
        </div>
        <div id="bank">
          <p style={{ gridArea: "1/1/2/5", marginBottom: 0 }}>Bank</p>
          <p>Heater</p>
          <Checkbox action={this.props.turnBank} />
          <p>Piano</p>
        </div>
        <div id="info-monitor">{this.props.info}</div>
      </div>
    );
  }
}

export default Display;
