class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      bank: piano,
      power: true,
      displayHeight: null,
      volume: 0.3,
      info: ""
    };

    this.padsHeightRef = React.createRef();
  }

  componentDidMount = () => {
    this.setState({
      displayHeight: this.padsHeightRef.current.clientHeight
    });
  };

  onTurnPower = val => {
    this.setState({ power: val, info: val === true ? "ON" : "OFF" });
    setTimeout(() => this.setState({info: ''}), 2000)
  };

  onTurnBank = val => {
    val === true
      ? this.setState({ bank: piano, info: "Smooth Piano Kit" })
      : this.setState({ bank: heater, info: "Heater Kit" });
  };

  volume = val => {
    this.setState({ volume: val / 100, info: `Volume: ${val}` });
    setTimeout(() => this.setState({info: ''}), 2000)
  };

  nameOnKeyDown = val => {
    this.setState({ info: val });
  };

  render() {
    const drums = this.state.bank.map((obj, i, arr) => {
      return (
        <DrumPad
          key={obj.id}
          bankObj={obj}
          statusPower={this.state.power}
          volume={this.state.volume}
          nameOnKeyDown={this.nameOnKeyDown}
        />
      );
    });
    return (
      <div id="drum-machine">
        <div id="drumpads" ref={this.padsHeightRef}>
          {drums}
        </div>
        <div id="display" style={{ height: `${this.state.displayHeight}px` }}>
          <Display
            turnPower={this.onTurnPower}
            turnBank={this.onTurnBank}
            volumeAdj={this.volume}
            info={this.state.info}
          />
        </div>
      </div>
    );
  }
}
ReactDOM.render(<App />, document.querySelector("#root"));
