import React from "react";
const activate = {
  backgroundColor: '#ba0160',
  border: '5px solid #ba0160',
  borderRadius: '5px'
}

const inactivate = {
  backgroundColor: '#292828',
  border: '5px solid #ba0160',
  borderRadius: '5px'
}


class DrumPad extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      padStyle: inactivate
    }
    this.audioRef = React.createRef();
  }

  componentDidUpdate = () => {
    this.audioRef.current.volume = this.props.volume
  }

  componentDidMount = () => {
    document.addEventListener("keydown", this.onKeyDown);
  }

  componentWillUnmount = () => {
    document.removeEventListener("keydown", this.onKeyDown);
  }

  activatePad = () => {
    if(this.state.padStyle.backgroundColor === '#ba0160') {
      this.setState({padStyle: inactivate});
    } else {this.setState({padStyle: activate});}
  }

  onSound = () => {
    ;
    if(this.props.statusPower) {
      const audio = this.audioRef.current;
      audio.pause();
      audio.currentTime = 0;
      audio.play();
      this.activatePad();
      setTimeout(() => this.activatePad(), 100)
      this.props.nameOnKeyDown(this.props.bankObj.name);
    }
  }

  onKeyDown = event => {
    
    if(this.props.bankObj.buttonCode === event.keyCode) {
      this.onSound();
    }
  }

  render() {
    const { id, button, name, url } = this.props.bankObj;
    return (
      <div
        className="drum-pad"
        id={id}
        onClick={this.onSound}
        style={this.state.padStyle}
      >
        <p style={{color: '#ba0160', fontSize:'30px'}}>{button}
        </p>
        <audio
          src={url}
          id={button}
          autoPlay={false}
          name={name}
          ref={this.audioRef}
        />
      </div>
    );
  }
}

export default DrumPad;
